
const initState = {
    filters: {
        minScore: 315,
        level: 20,
        arrange: 0,
    },
    price: {
        raca:0,
        elmon:0
    },
    filtersElemon:{
        name:'',
        id:'',
        rarity: '',
        purity:'',
        price:'',
    }
}

const rootReducer = (state = initState, action) => {
    switch (action.type) {
        case 'handleFilters':
            return {
                ...state,
                filters: {
                    ...state.filters,
                    minScore: action.payload.minScore,
                    level: action.payload.level
                }
            }
        case 'handleArrange':
            return {
                ...state,
                filters: {
                    ...state.filters,
                    arrange: action.payload
                }
            }
        case 'updatePrice':
            return {
                ...state,
                price: action.payload
            }      
        default:
            return state;
    }
}
export default rootReducer;

